import React, { useState, useRef } from 'react';
import {
  Navbar,
  Modal,
  InputGroup,
  FormControl,
  Form,
  Button,
  Card,
  CardDeck,
  Container,
  Overlay,
  Tooltip,
} from 'react-bootstrap';
import YouTube from 'react-youtube';
import { SketchPicker } from 'react-color';

function Content() {
  const [show, setShow] = useState(false);
  const target = useRef(null);
  const [jumlahCard, setJumlahCard] = useState(1);
  const [letakText, setLetakText] = useState('Tengah');
  const [modalContent, setModalContent] = useState(false);
  const [containerColor, setContainerColor] = useState('#F4F2F2');
  const [contextColor, setContextColor] = useState('#FFFFFF');
  const [fontColorMain, setFontColorMain] = useState('#FFFFFF');
  const [textMain, setTextMain] = useState('');
  const [gambarMain, setGambarMain] = useState('');
  const [gambarCard1, setGambarCard1] = useState('');
  const [gambarCard2, setGambarCard2] = useState('');
  const [gambarCard3, setGambarCard3] = useState('');
  const [judulCard1, setJudulCard1] = useState('');
  const [judulCard2, setJudulCard2] = useState('');
  const [judulCard3, setJudulCard3] = useState('');
  const [isiCard1, setIsiCard1] = useState('');
  const [isiCard2, setIsiCard2] = useState('');
  const [isiCard3, setIsiCard3] = useState('');
  const [fontCard1, setFontCard1] = useState('#555555');
  const [fontCard2, setFontCard2] = useState('#555555');
  const [fontCard3, setFontCard3] = useState('#555555');
  const [youtube, setYoutube] = useState('');
  const opts = {
    height: '720px',
    width: '90%',
    playerVars: {
      autoplay: 0,
    },
  };
  return (
    <div
      style={{ minHeight: '850px', backgroundColor: containerColor }}
      ref={target}
      onMouseEnter={() => setShow(true)}
      onMouseLeave={() => setShow(false)}
    >
      <Overlay target={target.current} show={show} placement="top">
        {(props) => <Tooltip {...props}>Klik untuk edit Konten</Tooltip>}
      </Overlay>
      <div
        onClick={() => setModalContent(true)}
        style={{
          minHeight: '850px',
          maxWidth: '80%',
          backgroundColor: contextColor,
          margin: '0 auto',
        }}
      >
        <div
          style={{
            height: '500px',
            backgroundImage: `url(${gambarMain}), url("https://via.placeholder.com/500")`,
            backgroundSize: 'cover',
            display: 'flex',
            textAlign: 'center',
            alignItems: 'center',
            marginBottom: '100px',
            color: fontColorMain,
          }}
        >
          <Container style={{ verticalAlign: 'middle' }}>
            {letakText === 'Tengah' && <text>{textMain}</text>}
            {letakText !== 'Tengah' && (
              <div className="row">
                <div className="col-md-6" style={{ textAlign: 'left' }}>
                  {letakText === 'Kiri' && <text>{textMain}</text>}
                </div>
                <div className="col-md-6" style={{ textAlign: 'right' }}>
                  {letakText === 'Kanan' && <text>{textMain}</text>}
                </div>
              </div>
            )}
          </Container>
        </div>
        {jumlahCard > 0 && (
          <Container style={{ marginBottom: '100px' }}>
            <CardDeck>
              {(jumlahCard == 1 || jumlahCard == 2 || jumlahCard == 3) && (
                <Card
                  style={{
                    backgroundImage: `url(${gambarCard1}), url("https://via.placeholder.com/500")`,
                    backgroundSize: 'cover',
                    color: fontCard1,
                  }}
                >
                  <Card.Body style={{ paddingTop: '100px' }}>
                    <Card.Title>{judulCard1}</Card.Title>
                    <Card.Text>{isiCard1}</Card.Text>
                  </Card.Body>
                </Card>
              )}
              {(jumlahCard == 2 || jumlahCard == 3) && (
                <Card
                  style={{
                    backgroundImage: `url(${gambarCard2}), url("https://via.placeholder.com/500")`,
                    backgroundSize: 'cover',
                    color: fontCard2,
                  }}
                >
                  <Card.Body style={{ paddingTop: '100px' }}>
                    <Card.Title>{judulCard2}</Card.Title>
                    <Card.Text>{isiCard2}</Card.Text>
                  </Card.Body>
                </Card>
              )}
              {jumlahCard == 3 && (
                <Card
                  style={{
                    backgroundImage: `url(${gambarCard3}), url("https://via.placeholder.com/500")`,
                    backgroundSize: 'cover',
                    color: fontCard3,
                  }}
                >
                  <Card.Body style={{ paddingTop: '100px' }}>
                    <Card.Title>{judulCard3}</Card.Title>
                    <Card.Text>{isiCard3}</Card.Text>
                  </Card.Body>
                </Card>
              )}
            </CardDeck>
          </Container>
        )}
        {youtube != '' && (
          <div>
            <YouTube videoId={youtube} opts={opts} />
          </div>
        )}
      </div>

      <Modal
        show={modalContent}
        onHide={() => setModalContent(false)}
        size="sm"
        dialogClassName="edit"
        className="edit"
      >
        <Modal.Header closeButton>
          <Modal.Title>Editing Content</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="container">
            <Form>
              <Form.Group>
                <label htmlFor="basic-url">
                  <b>Gambar Utama:</b>
                </label>
                <Form.File
                  id="exampleFormControlFile1"
                  onChange={(e) =>
                    setGambarMain(URL.createObjectURL(e.target.files[0]))
                  }
                />
              </Form.Group>
            </Form>
            <label htmlFor="basic-url">
              <b>Isi Text Gambar:</b>
            </label>
            <InputGroup className="mb-3">
              <FormControl
                as="textarea"
                value={textMain}
                onChange={(e) => setTextMain(e.target.value)}
                placeholder="Isi paragraf untuk gambar disini"
              />
            </InputGroup>
            <label htmlFor="basic-url">
              <b>Letak Text:</b>
            </label>
            <InputGroup className="mb-3">
              <Form.Control
                as="select"
                onChange={(e) => setLetakText(e.target.value)}
                value={letakText}
              >
                <option>Kiri</option>
                <option>Tengah</option>
                <option>Kanan</option>
              </Form.Control>
            </InputGroup>
            <label htmlFor="basic-url">
              <b>Warna Huruf Gambar:</b>
            </label>
            <InputGroup className="mb-3">
              <SketchPicker
                color={fontColorMain}
                onChange={(color) => setFontColorMain(color.hex)}
              ></SketchPicker>
            </InputGroup>
            <label htmlFor="basic-url">
              <b>Warna Container:</b>
            </label>
            <InputGroup className="mb-3">
              <SketchPicker
                color={containerColor}
                onChange={(color) => setContainerColor(color.hex)}
              ></SketchPicker>
            </InputGroup>
            <label htmlFor="basic-url">
              <b>Warna Konteks:</b>
            </label>
            <InputGroup className="mb-3">
              <SketchPicker
                color={contextColor}
                onChange={(color) => setContextColor(color.hex)}
              ></SketchPicker>
            </InputGroup>

            <label htmlFor="basic-url">
              <b>Jumlah Kotak:</b>
            </label>
            <InputGroup className="mb-3">
              <Form.Control
                as="select"
                onChange={(e) => setJumlahCard(e.target.value)}
                value={jumlahCard}
              >
                <option value={0}>0</option>
                <option>1</option>
                <option value={2}>2</option>
                <option value={3}>3</option>
              </Form.Control>
            </InputGroup>
            {(jumlahCard == 1 || jumlahCard == 2 || jumlahCard == 3) && (
              <div className="block-example border border-light">
                <label htmlFor="basic-url">Kotak Pertama:</label>
                <InputGroup className="mb-3">
                  <FormControl
                    value={judulCard1}
                    onChange={(e) => setJudulCard1(e.target.value)}
                    placeholder="Judul Kotak Pertama"
                  />
                </InputGroup>
                <InputGroup>
                  <FormControl
                    as="textarea"
                    value={isiCard1}
                    onChange={(e) => setIsiCard1(e.target.value)}
                    placeholder="Isi Kotak Pertama"
                  />
                  <br />
                  <Form>
                    <Form.Group>
                      <Form.File
                        id="exampleFormControlFile1"
                        onChange={(e) =>
                          setGambarCard1(URL.createObjectURL(e.target.files[0]))
                        }
                      />
                    </Form.Group>
                  </Form>
                  <SketchPicker
                    color={fontCard1}
                    onChange={(color) => setFontCard1(color.hex)}
                  ></SketchPicker>
                </InputGroup>
              </div>
            )}

            {(jumlahCard == 2 || jumlahCard == 3) && (
              <div className="block-example border border-light">
                <label htmlFor="basic-url">Kotak Kedua:</label>
                <InputGroup className="mb-3">
                  <FormControl
                    value={judulCard2}
                    onChange={(e) => setJudulCard2(e.target.value)}
                    placeholder="Judul Kotak Kedua"
                  />
                </InputGroup>
                <InputGroup>
                  <FormControl
                    as="textarea"
                    value={isiCard2}
                    onChange={(e) => setIsiCard2(e.target.value)}
                    placeholder="Isi Kotak Kedua"
                  />
                  <br />
                  <Form>
                    <Form.Group>
                      <Form.File
                        id="exampleFormControlFile1"
                        onChange={(e) =>
                          setGambarCard2(URL.createObjectURL(e.target.files[0]))
                        }
                      />
                    </Form.Group>
                  </Form>
                  <SketchPicker
                    color={fontCard2}
                    onChange={(color) => setFontCard2(color.hex)}
                  ></SketchPicker>
                </InputGroup>
              </div>
            )}

            {jumlahCard == 3 && (
              <div className="block-example border border-light">
                <label htmlFor="basic-url">Kotak Ketiga:</label>
                <InputGroup className="mb-3">
                  <FormControl
                    value={judulCard3}
                    onChange={(e) => setJudulCard3(e.target.value)}
                    placeholder="Judul Kotak Ketiga"
                  />
                </InputGroup>
                <InputGroup>
                  <FormControl
                    as="textarea"
                    value={isiCard3}
                    onChange={(e) => setIsiCard3(e.target.value)}
                    placeholder="Isi Kotak Ketiga"
                  />
                  <br />
                  <Form>
                    <Form.Group>
                      <Form.File
                        id="exampleFormControlFile1"
                        onChange={(e) =>
                          setGambarCard3(URL.createObjectURL(e.target.files[0]))
                        }
                      />
                    </Form.Group>
                  </Form>
                  <SketchPicker
                    color={fontCard3}
                    onChange={(color) => setFontCard3(color.hex)}
                  ></SketchPicker>
                </InputGroup>
              </div>
            )}
            <br />
            <label htmlFor="basic-url">
              <b>Link Youtube:</b>
            </label>
            <InputGroup className="mb-3">
              <FormControl
                value={youtube}
                onChange={(e) => setYoutube(e.target.value)}
                placeholder="Masukan IDnya Saja"
              />
            </InputGroup>
          </div>
          <Modal.Body></Modal.Body>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default Content;
