import React, { useState, useRef } from 'react';
import {
  Modal,
  Button,
  InputGroup,
  FormControl,
  Overlay,
  Tooltip,
} from 'react-bootstrap';
import { SketchPicker } from 'react-color';

function Footer() {
  const [show, setShow] = useState(false);
  const target = useRef(null);
  const [modalFooter, setModalFooter] = useState(false);
  const [github, setGithub] = useState('');
  const [facebook, setFacebook] = useState('');
  const [instagram, setInstagram] = useState('');
  const [linkedin, setLinkedin] = useState('');
  const [leftPanel, setLeftPanel] = useState('');
  const [rightPanel, setRightPanel] = useState('');
  const [copyright, setCopyright] = useState(
    'Copyright Â© 2020 ramaadhiguna. All rights reserved.'
  );
  const [fontColor, setFontColor] = useState('#979797');
  const [footerColor, setFooterColor] = useState('#F4F2F2');
  function NewlineText(props) {
    const text = props.text;
    const newText = text.split('\n').map((str) => (
      <text>
        {str}
        <br />
      </text>
    ));
    return newText;
  }
  return (
    <div
      ref={target}
      onMouseEnter={() => setShow(true)}
      onMouseLeave={() => setShow(false)}
      style={{
        color: fontColor,
        backgroundColor: footerColor,
        minHeight: '75px',
      }}
    >
      <Overlay target={target.current} show={show} placement="top">
        {(props) => <Tooltip {...props}>Klik untuk edit Footer</Tooltip>}
      </Overlay>
      <div
        className="form-inline"
        onClick={() => setModalFooter(true)}
        //style={{ height: '100px' }}
      >
        <div className="col-md-4" style={{ textAlign: 'left' }}>
          <NewlineText text={leftPanel} />
        </div>
        <div className="col-md-4" style={{ textAlign: 'center' }}>
          {copyright}
        </div>
        <div className="col-md-4" style={{ textAlign: 'right' }}>
          <NewlineText text={rightPanel} />
          {github != '' && (
            <a
              target="blank"
              href={github}
              style={{ color: fontColor, marginLeft: '10px' }}
            >
              <i className="fa fa-github"></i>
            </a>
          )}
          {facebook != '' && (
            <a
              target="blank"
              href={facebook}
              style={{ color: fontColor, marginLeft: '10px' }}
            >
              <i className="fa fa-facebook"></i>
            </a>
          )}
          {instagram != '' && (
            <a
              target="blank"
              href={instagram}
              style={{ color: fontColor, marginLeft: '10px' }}
            >
              <i className="fa fa-instagram"></i>
            </a>
          )}
          {linkedin != '' && (
            <a
              target="blank"
              href={linkedin}
              style={{ color: fontColor, marginLeft: '10px' }}
            >
              <i className="fa fa-linkedin"></i>
            </a>
          )}
        </div>
      </div>
      <style type="text/css">
        {`
          .modal-dialog{
            left: 42% !important;
            width: 14% !important;
          }
          .modal-backdrop
          {
            opacity:0.001 !important;
          }
        `}
      </style>
      <Modal show={modalFooter} onHide={() => setModalFooter(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Editing Footer</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <label htmlFor="basic-url">Copyright:</label>
          <InputGroup className="mb-3">
            <FormControl
              as="textarea"
              value={copyright}
              onChange={(e) => setCopyright(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Footer Color:</label>
          <InputGroup className="mb-3">
            <SketchPicker
              color={footerColor}
              onChange={(color) => setFooterColor(color.hex)}
            ></SketchPicker>
          </InputGroup>
          <label htmlFor="basic-url">Font Color:</label>
          <InputGroup className="mb-3">
            <SketchPicker
              color={fontColor}
              onChange={(color) => setFontColor(color.hex)}
            ></SketchPicker>
          </InputGroup>
          <label htmlFor="basic-url">Left Panel:</label>
          <InputGroup className="mb-3">
            <FormControl
              as="textarea"
              value={leftPanel}
              onChange={(e) => setLeftPanel(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Right Panel:</label>
          <InputGroup className="mb-3">
            <FormControl
              as="textarea"
              value={rightPanel}
              onChange={(e) => setRightPanel(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Github Link:</label>
          <InputGroup className="mb-3">
            <FormControl
              value={github}
              onChange={(e) => setGithub(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Facebook Link:</label>
          <InputGroup className="mb-3">
            <FormControl
              value={facebook}
              onChange={(e) => setFacebook(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Instagram Link:</label>
          <InputGroup className="mb-3">
            <FormControl
              value={instagram}
              onChange={(e) => setInstagram(e.target.value)}
            />
          </InputGroup>
          <label htmlFor="basic-url">Linkedin Link:</label>
          <InputGroup className="mb-3">
            <FormControl
              value={linkedin}
              onChange={(e) => setLinkedin(e.target.value)}
            />
          </InputGroup>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default Footer;
