import React, { useState, useRef } from 'react';
import {
  Navbar,
  Modal,
  InputGroup,
  FormControl,
  Nav,
  Button,
  Overlay,
  Tooltip,
} from 'react-bootstrap';
import { SketchPicker } from 'react-color';

function Header() {
  const [show, setShow] = useState(false);
  const target = useRef(null);
  const [modalHeader, setModalHeader] = useState(false);
  const [name, setName] = useState('Test untuk Kirim.Email');
  const [linkList, setLinkList] = useState('HOME');
  const [fontColor, setFontColor] = useState('#000000');
  const [navColor, setNavColor] = useState('#FFFFFF');
  function LinkList(props) {
    const text = props.text;
    const newText = text.split('\n').map((str) => (
      <Nav.Link className="nav-link" style={{ color: fontColor }}>
        {str}
      </Nav.Link>
    ));
    return newText;
  }
  return (
    <div
      ref={target}
      onMouseEnter={() => setShow(true)}
      onMouseLeave={() => setShow(false)}
    >
      <Overlay target={target.current} show={show} placement="bottom">
        {(props) => <Tooltip {...props}>Klik untuk edit Header</Tooltip>}
      </Overlay>
      <Navbar
        style={{ backgroundColor: navColor }}
        onClick={() => setModalHeader(true)}
      >
        <Navbar.Brand className="mr-auto" style={{ color: fontColor }}>
          {name}
        </Navbar.Brand>
        <Nav className="ml-auto">
          <LinkList text={linkList} />
        </Nav>
      </Navbar>

      <style type="text/css">
        {`
          .modal-dialog{
            left: 42% !important;
            width: 14% !important;
          }
          .modal-backdrop
          {
            opacity:0.001 !important;
          }
        `}
      </style>
      <Modal
        show={modalHeader}
        onHide={() => setModalHeader(false)}
        size="sm"
        dialogClassName="edit"
        className="edit"
      >
        <Modal.Header closeButton>
          <Modal.Title>Editing Header</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="container">
            <label htmlFor="basic-url">Judul Web:</label>
            <InputGroup className="mb-3">
              <FormControl
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </InputGroup>
            <label htmlFor="basic-url">Warna Header:</label>
            <InputGroup className="mb-3">
              <SketchPicker
                color={navColor}
                onChange={(color) => setNavColor(color.hex)}
              ></SketchPicker>
            </InputGroup>
            <label htmlFor="basic-url">Warna Huruf:</label>
            <InputGroup className="mb-3">
              <SketchPicker
                color={fontColor}
                onChange={(color) => setFontColor(color.hex)}
              ></SketchPicker>
            </InputGroup>
            <label htmlFor="basic-url">
              Daftar link Header:
              <br />
              <small style={{ color: 'red' }}>*new line untuk link baru</small>
            </label>
            <InputGroup className="mb-3">
              <FormControl
                as="textarea"
                value={linkList}
                onChange={(e) => setLinkList(e.target.value)}
                placeholder="New Line untuk membuat link baru"
              />
            </InputGroup>
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default Header;
